import Vue from 'vue'

import Router from 'vue-router'

import Users from '@/components/AllUsersPage'
import User from '@/components/UsersPage'

import addTask from '@/components/AddTask'
import listPage from '@/components/ListPage'

import SignUp from '@/components/SignUp'
import toDoList_1 from '@/components/SignUp'
Vue.use(Router)


export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/todoList/type1',
      name: 'signUp',
      component: SignUp      
    },
    {
      path: '/',
      name: 'listPage',
      component: listPage
    },
    {
      path: '/user',
      name: 'user',
      component: Users
    },
    {
      path: '/user/:id',
      name: 'detail',
      component: User
    },
    {
      path: '/listPage',
      name: 'listPage',
      component: listPage
    }, 
    {
      path: '/addTask',
      name: 'addTask',
      component: addTask
    },
    {
      path: '/addTask/save',
      name: 'taskSave',
      component: addTask
    }    
  ]
})



